import {profileCollect} from '@/network/getProfileData.js'
import {unCollectProduct2} from '@/network/detail'
import { mapGetters } from 'vuex'
export default {
	data() {
		return {
			winSize:false,///* 显示遮罩 */
			windowHeight:0,//高度
			recordList:[],
			pages:1
		}
	},
	onLoad() {
		this.getWindowSize();
		if(!this.isToken){
			uni.navigateTo({
				url:'../../login/login'
			})
		}
	},
	onShow() {
		this.profileCollect(this.pages,this.isToken)
	},
	methods: {
		// 取消收藏
		unCollect(vo,index){
			uni.showModal({
				title:'是否取消收藏',
				content:'',
				success:(res) =>  {
					if(res.confirm){
						
						// 取消收藏
						unCollectProduct2(vo.pid,this.isToken).then(res => {
							if(res.data.code == 200){
								uni.showToast({
									title:'已取消',
									icon:'none',
									success:(res)=>  {
										this.recordList.splice(index,1)
									}
								})
							}
						})
					}
				}
			})
		},
		// 获取数据源
		profileCollect(page,token){
			profileCollect(page,token)
				.then(res => {
					console.log(res)
					if(res.data.code == 200){
						this.recordList = res.data.data
						console.log(this.recordList)
					}
				})
		},
		/* 获取窗口尺寸 */
		getWindowSize() {
			uni.getSystemInfo({
				success: (res) => {
					this.windowHeight = res.windowHeight
				}
			})
		},
		//点击删除
		onLongPress(key){
			var that = this
			that.recordList[key].popu = true
			that.winSize = true
			
		},
		
		//点击遮罩
		winSizeClick(){
			var that = this
			that.winSize = false
			that.recordList.forEach(function(item){
				item.popu = false
			})
		},
		
		//删除记录
		delListClick(){
			this.winSizeClick()
		},
		
	},
	computed:{
		...mapGetters(['isToken']),
	}
}
